      let seoul_bike_url = 'https://9e240d7v0k.execute-api.ap-northeast-2.amazonaws.com/api/seoul_bike/get_station'
      this.$http
        .post(seoul_bike_url, { latitude: centerLat, longitude: centerLng, distance: 500 })
        .then(function (response) {
          response.data.filter(
            function (data) {
              // ÎßàÏª§Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
              var markerPosition = new kakao.maps.LatLng(data['stationLatitude'], data['stationLongitude'])

              var imageSrc = require(`@/assets/green_marker.png`) // ÎßàÏª§Ïù¥ÎØ∏ÏßÄÏùò Ï£ºÏÜåÏûÖÎãàÎã§    
              var imageSize = new kakao.maps.Size(38, 52)         // ÎßàÏª§Ïù¥ÎØ∏ÏßÄÏùò ÌÅ¨Í∏∞ÏûÖÎãàÎã§
              var imageOption = { offset: new kakao.maps.Point(19, 32) }; // ÎßàÏª§Ïù¥ÎØ∏ÏßÄÏùò ÏòµÏÖòÏûÖÎãàÎã§. ÎßàÏª§Ïùò Ï¢åÌëúÏôÄ ÏùºÏπòÏãúÌÇ¨ Ïù¥ÎØ∏ÏßÄ ÏïàÏóêÏÑúÏùò Ï¢åÌëúÎ•º ÏÑ§Ï†ïÌï©ÎãàÎã§.

              // ÎßàÏª§Ïùò Ïù¥ÎØ∏ÏßÄÏ†ïÎ≥¥Î•º Í∞ÄÏßÄÍ≥† ÏûàÎäî ÎßàÏª§Ïù¥ÎØ∏ÏßÄÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§
              var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption)

              // ÎßàÏª§Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
              var marker = new kakao.maps.Marker({
                position: markerPosition,
                image: markerImage // ÎßàÏª§Ïù¥ÎØ∏ÏßÄ ÏÑ§Ï†ï 
              });

              // ÎßàÏª§Í∞Ä ÏßÄÎèÑ ÏúÑÏóê ÌëúÏãúÎêòÎèÑÎ°ù ÏÑ§Ï†ïÌï©ÎãàÎã§
              marker.setMap(map);

              // Ïª§Ïä§ÌÖÄ Ïò§Î≤ÑÎ†àÏù¥Ïóê ÌëúÏ∂úÎê† ÎÇ¥Ïö©ÏúºÎ°ú HTML Î¨∏ÏûêÏó¥Ïù¥ÎÇò document elementÍ∞Ä Í∞ÄÎä•Ìï©ÎãàÎã§
              var content = '<div class="customoverlay">' +
                '    <span class="title">' + data['parkingQRBikeCnt'] + '</span>' +
                '</div>';

              // Ïª§Ïä§ÌÖÄ Ïò§Î≤ÑÎ†àÏù¥Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
              var customOverlay = new kakao.maps.CustomOverlay({
                map: map,
                position: markerPosition,
                content: content,
                yAnchor: 1
              });
              customOverlay.setMap(map)

            })
        });
    },
    initMap() {
      let home = new kakao.maps.LatLng(37.503866433821464, 127.09514153848163);

      let mapContainer = document.getElementById('map'), // ÏßÄÎèÑÎ•º ÌëúÏãúÌï† div
        mapOption = {
          center: home, // ÏßÄÎèÑÏùò Ï§ëÏã¨Ï¢åÌëú
          level: 4, // ÏßÄÎèÑÏùò ÌôïÎåÄ Î†àÎ≤®
        };
      this.map = new kakao.maps.Map(mapContainer, mapOption);
      // ÏßÄÎèÑÎ•º ÌÅ¥Î¶≠Ìïú ÏúÑÏπòÏóê ÌëúÏ∂úÌï† ÎßàÏª§ÏûÖÎãàÎã§
      this.homeMarker = new kakao.maps.Marker({
        // ÏßÄÎèÑ Ï§ëÏã¨Ï¢åÌëúÏóê ÎßàÏª§Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
        position: home
      });
      // ÏßÄÎèÑÏóê ÎßàÏª§Î•º ÌëúÏãúÌï©ÎãàÎã§
      this.homeMarker.setMap(this.map);

      // Ï£ºÏÜå-Ï¢åÌëú Î≥ÄÌôò Í∞ùÏ≤¥Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
      this.geocoder = new kakao.maps.services.Geocoder();

      // ÌÅ¥Î¶≠Ìïú ÏúÑÏπòÏóê ÎåÄÌïú Ï£ºÏÜåÎ•º ÌëúÏãúÌï† Ïù∏Ìè¨ÏúàÎèÑÏö∞ÏûÖÎãàÎã§
      this.infowindow = new kakao.maps.InfoWindow({ zindex: 1 });
      this.homeInfoWindow = new kakao.maps.InfoWindow({ zindex: 1 });

      // Ïù∏Ìè¨ÏúàÎèÑÏö∞Ïóê ÌÅ¥Î¶≠Ìïú ÏúÑÏπòÏóê ÎåÄÌïú Î≤ïÏ†ïÎèô ÏÉÅÏÑ∏ Ï£ºÏÜåÏ†ïÎ≥¥Î•º ÌëúÏãúÌï©ÎãàÎã§
      this.homeInfoWindow.setContent('<div style="padding:5px;text-overflow: ellipsis;overflow: hidden;white-space: nowrap;">ÏÜ°ÌååÍµ¨ ÏÇºÏ†ÑÎèô <br> Î∞±Ï†úÍ≥†Î∂ÑÎ°ú 31Í∏∏ 11-6</div>');
      this.homeInfoWindow.open(this.map, this.homeMarker);

      // let roadviewContainer = document.getElementById('roadview'); //Î°úÎìúÎ∑∞Î•º ÌëúÏãúÌï† div
      // let roadview = new kakao.maps.Roadview(roadviewContainer); //Î°úÎìúÎ∑∞ Í∞ùÏ≤¥
      // let roadviewClient = new kakao.maps.RoadviewClient(); //Ï¢åÌëúÎ°úÎ∂ÄÌÑ∞ Î°úÎìúÎ∑∞ ÌååÎÖ∏IDÎ•º Í∞ÄÏ†∏Ïò¨ Î°úÎìúÎ∑∞ helperÍ∞ùÏ≤¥

      // // ÌäπÏ†ï ÏúÑÏπòÏùò Ï¢åÌëúÏôÄ Í∞ÄÍπåÏö¥ Î°úÎìúÎ∑∞Ïùò panoIdÎ•º Ï∂îÏ∂úÌïòÏó¨ Î°úÎìúÎ∑∞Î•º ÎùÑÏö¥Îã§.
      // roadviewClient.getNearestPanoId(home, 50, function (panoId) {
      //   roadview.setPanoId(panoId, home); //panoIdÏôÄ Ï§ëÏã¨Ï¢åÌëúÎ•º ÌÜµÌï¥ Î°úÎìúÎ∑∞ Ïã§Ìñâ
      // });

      this.seoulBike(this.map);
    },
  }
}
</script>

